<template>
<div class="addEmployee">
	<el-button :type="type" size="small" :icon="add&&!fromResume?'el-icon-plus':''" @click="getDateById">{{text}}</el-button>
	<el-dialog :modal='false' :fullscreen="true" :visible.sync="open" :title="(add?'':'')+''"  :close-on-click-modal="false" @close="resert('aadForm')">
		<div class="post-rel clearfix allwrap" style="overflow: hidden;" v-loading.body="saveLoading" :element-loading-text="loadingText">
			<ul class="tabs">
				<li v-for="(tab,index) in tabList" :key="index" v-show="tab.show"
					:class="{active:activeInd==index}" @click="activeInd=index">{{tab.label}}</li>
			</ul>
			<div class="content">
				<el-form  :model="form" v-show="open&&activeInd<2" inline :rules="rules" label-position="right" label-width="110px" ref="aadForm">
					<el-row v-show="activeInd==0">
						<el-col :span="8">
							<el-form-item prop="info.name" label=""  
								:rules="rules['info.name']">
								<el-input size="small" v-model="form.info.name" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.code" label=""  :rules="rules['info.code']">
								<el-input size="small" v-model="form.info.code" placeholder="" :readonly="!add"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.mobile" label="" :rules="rules['info.mobile']" >
								<el-input size="small" v-model="form.info.mobile" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.deptId" label=""  :rules="rules['info.deptId']">
								<hr-dept v-model="form.info.deptId" size="small" :multiple="false" :filter-org="true"></hr-dept>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.jobId" label=""  :rules="rules['info.jobId']">
								<hr-position size="small" v-model="form.info.jobId" :related-dept="form.info.deptId" :filter-org="true"></hr-position>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.jobOrgId" label=""  :rules="rules['info.jobOrgId']">
								<el-select size="small" clearable v-model="form.info.jobOrgId" filterable placeholder="">
									<el-option v-for="job in sels.jobOrgs"
										:key="job.id"
	      								:label="job.name"
	     								:value="job.id">
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.workSystemId" label="" :rules="rules['info.workSystemId']">
								<el-select size="small" clearable v-model="form.info.workSystemId" filterable placeholder="">
									<el-option v-for="type in sels.workTypes"
										:key="type.id"
	      								:label="type.name"
	     								:value="type.id">
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.idNum" label=""  :rules="rules['info.idNum']">
								<el-input @blur="idCardBlur" size="small" v-model="form.info.idNum" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.dateOfBirth" label="" :rules="rules['info.dateOfBirth']">
								<el-input size="small" v-model="form.info.dateOfBirth" placeholder="" readonly></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.sex" label=""  required>
								<el-radio-group v-model="form.info.sex">
								    <el-radio disabled :label="1"></el-radio>
								    <el-radio disabled :label="2"></el-radio>
							    </el-radio-group>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.jobState" label="" required>
								<el-radio-group v-model="form.info.jobState" :disabled="!add">
								    <el-radio :label="1"></el-radio>
								    <el-radio :label="2"></el-radio>
								    <el-radio :label="3" disabled></el-radio>
							    </el-radio-group>
							</el-form-item>
						</el-col>
						
						<el-col :span="8">
							<el-form-item prop="info.entryDate" label="" :rules="rules['info.entryDate']">
								<el-date-picker
								    v-model="form.info.entryDate"
								    type="date"
								    size="small"
								    clearable
								    placeholder="">
								</el-date-picker>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.probitionPeriod" label="" :rules="rules['info.probitionPeriod']">
								<el-input size="small" :disabled="!add" v-model="form.info.probitionPeriod" placeholder="/"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.formalDate" label="" :rules="rules['info.formalDate']">
								<el-date-picker
								    v-model="form.info.formalDate"
								    type="date"
								    size="small"
								    clearable
								    :picker-options="formalDatePickerOptions"
								    placeholder="">
								</el-date-picker>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.quitDate" label="" :rules="rules['info.quitDate']">
								<el-date-picker
									disabled
								    v-model="form.info.quitDate"
								    type="date"
								    size="small"
								    clearable
								    placeholder="">
								</el-date-picker>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.email" label=""  :rules="rules['info.email']">
								<el-input size="small" v-model="form.info.email" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.bankCardNo" label="" :rules="rules['info.bankCardNo']">
								<el-input size="small" v-model="form.info.bankCardNo" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.workMonths" label=""  >
								<div class="el-input el-input--small">
									<el-input size="small" v-model="form.info.workMonths" placeholder="" style="width: 150px;margin-right: 5px;"></el-input>
								</div>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.requireSettlement" label=""  >
								<el-radio-group v-model="form.info.requireSettlement">
								    <el-radio label="Y"></el-radio>
								    <el-radio label="N"></el-radio>
							    </el-radio-group>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.requireAttendance" label=""  >
								<el-radio-group v-model="form.info.requireAttendance">
								    <el-radio label="Y"></el-radio>
								    <el-radio label="N"></el-radio>
							    </el-radio-group>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="baseInfo.nativeStr" label=""  :rules="[{required: true, message: '', trigger: 'blur'}]">
								<el-input placeholder="" v-model="form.baseInfo.nativeStr"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="" prop="height" :rules="[{pattern:/^[1-9]\d*$/, message:'', trigger: 'blur'}]">
					         	<el-input placeholder="(cm)" v-model.number="form.baseInfo.height"></el-input>
					        </el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="" prop="weight" :rules="[{pattern:/^[1-9]\d*$/, message:'', trigger: 'blur'}]">
					         	<el-input placeholder="" v-model.number="form.baseInfo.weight"></el-input>
					        </el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="" prop="politics">
					          <el-select v-model="form.baseInfo.politics">
					            <el-option v-for="(item, index) in politicsOptions"
					              :key="index" :label="item.label"
					              :value="item.label">
					            </el-option>
					          </el-select>
					        </el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="" prop="marriage">
					          <el-radio-group v-model="form.baseInfo.marriage">
					            <el-radio
					              v-for="(item,index) in checkMarriedList"
					              :label="item.value"
					              :key="index">
					              {{ item.label}}
					            </el-radio>
					          </el-radio-group>
					        </el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="" label-width="120px" prop="escuage">
					          <el-radio-group v-model="form.baseInfo.escuage">
					            <el-radio
					              v-for="(item, index) in soldierList"
					              :label="item.label"
					              :key="index">
					              {{ item.label }}
					            </el-radio>
					          </el-radio-group>
					        </el-form-item>
						</el-col>
					</el-row>
					<template v-if="canCreateSalary">
					<el-row v-show="activeInd==1">
						<el-col :span="8">
							<el-form-item prop="info.jobTypeName" label="">
								<el-input size="small" :value="form.info.jobTypeName" placeholder="" readonly></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.jobGradeName" label="">
								<el-input size="small" :value="form.info.jobGradeName" placeholder="" readonly></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.salaryId" label="" :rules="rules['info.salaryId']">
								<el-select size="small" clearable v-model="form.info.salaryId" filterable placeholder="" class="w130" style="width: 130px;margin-right: 5px;" >
									<el-option :value="0" label="0"></el-option>
									<el-option v-for="n in 10"
										:key="n"
	      								:label="''+n+''"
	     								:value="n">
									</el-option>
								</el-select>
								<span> {{form.info.salary}}</span>	
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.perfId" label="" >
								<el-select size="small" clearable v-model="form.info.perfId" filterable placeholder="" class="w130" style="width: 130px;margin-right: 5px;">
									<el-option :value="0" label="0"></el-option>
									<el-option v-for="n in 5"
										:key="n"
	      								:label="''+n+''"
	     								:value="n">
									</el-option>
								</el-select>
								<span> {{form.info.perf}}</span>
							</el-form-item>	
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.discount" label="" :rules="rules['info.discount']">
								<el-input size="small" v-model="form.info.discount" placeholder="(0-1)"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.wageForSeniorityAdd" label="" :rules="rules['info.wageForSeniorityAdd']">	
								<el-input size="small" v-model="form.info.wageForSeniorityAdd" placeholder="2"></el-input>					
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.otherAllowance" label="" :rules="rules['info.wageForSeniorityAdd']">
								<el-input size="small" v-model="form.info.otherAllowance" placeholder="2"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.trafficAllowance" label="" >
								<el-select size="small" allow-create clearable v-model="form.info.trafficAllowance" filterable placeholder="">
									<el-option v-for="(type,index) in sels.carMoneys"
										:key="index"
	      								:label="type.val"
	     								:value="type.val">
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.foodAllowance" label="">
								<el-select size="small" allow-create clearable v-model="form.info.foodAllowance" filterable placeholder="">
									<el-option v-for="(type,index) in sels.foodMoneys"
										:key="index"
	      								:label="type.val"
	     								:value="type.val">
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.tradeUnions" label="" >
								<el-radio-group v-model="form.info.tradeUnions">
								    <el-radio label="Y"></el-radio>
								    <el-radio label="N"></el-radio>
							    </el-radio-group>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.tradeUnionsFee" label="" >
								<el-select size="small" allow-create :disabled="form.info.tradeUnions=='N'" clearable v-model="form.info.tradeUnionsFee" filterable placeholder="">
									<el-option v-for="(type,index) in sels.vipMoneys"
										:key="index"
	      								:label="type.val"
	     								:value="type.val">
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="info.jobType" label="" >
								<el-select size="small" clearable v-model="form.info.jobType" filterable placeholder="">
									<el-option 
	      								label=""
	     								value="1">
									</el-option>
									<el-option 
	      								label=""
	     								value="2">
									</el-option>
									<el-option 
	      								label=""
	     								value="3">
									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="salary.amount" label="" :rules="rules['salary.amount']">
								<el-input size="small" v-model="form.salary.amount" placeholder="2"></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="salary.socialSecurityNo" label="" >
								<el-input size="small" v-model="form.salary.socialSecurityNo" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="salary.housingFundNo" label="" >
								<el-input size="small" v-model="form.salary.housingFundNo" placeholder=""></el-input>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							 <el-form-item label="" prop="salary.householdCode" >
                                <el-select v-model="form.salary.householdCode" placeholder="" size="small">
                                    <el-option v-for="item in sels.householdList" :key="item.code" :label="item.name" :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="salary.paymentUnit" label="" >
								<el-select v-model="form.salary.paymentUnit" placeholder="" size="small">
                                    <el-option v-for="item in sels.paymentUnitList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                                </el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="salary.cityCode" label="" >
                                <el-select v-model="form.salary.cityCode" placeholder="" size="small">
                                    <el-option v-for="item in sels.cityList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                                </el-select>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item prop="salary.accinfoName" label="" >
								<el-tooltip effect="dark" :content="'Bottom Center'+form.salary.accinfoName" v-if="form.salary.accinfoName&&form.salary.accinfoName.length>13">
									<el-input size="small" v-model="form.salary.accinfoName" placeholder="" readonly></el-input>
								</el-tooltip>
								<el-input v-else size="small" v-model="form.salary.accinfoName" placeholder="" readonly></el-input>
							</el-form-item>
						</el-col>
					</el-row>
					</template>
				</el-form>
				<contract   v-if="activeInd==2" :employee-code="form.info.code"></contract>
				<car-info v-if="activeInd==3" v-model="resurmeInfo" ref="carInfo"></car-info>
				<contact-info v-if="activeInd==4" v-model="resurmeInfo" ref="contactInfo"></contact-info>
				<family-info v-if="activeInd==5" v-model="resurmeInfo" ref="familyInfo"></family-info>
				<education-info v-if="activeInd==6" v-model="resurmeInfo" ref="educationInfo"></education-info>
				<work-info v-if="activeInd==7" v-model="resurmeInfo" ref="workInfo"></work-info>
				<skill-info v-if="activeInd==8" v-model="resurmeInfo" ref="skillInfo"></skill-info>
				<complaint-info v-if="activeInd==9" v-model="resurmeInfo" ref="complaintInfo"></complaint-info>
			</div>
		</div>
		<div style="text-align: center;">
			<el-button type="primary" @click="save('aadForm')" :loading="saveLoading"></el-button>
			<el-button @click="resert('aadForm')"></el-button>
		</div>
	</el-dialog>
</div>	
</template>

<script>
import {ctrlhr,sso,errorMessage} from '@/config/env'
import moment  from 'moment'
import hrDept  from '@/components/hr-dept'
import hrPosition  from '@/components/hr-position'
import contract  from '~/contract/contract'

import educationInfo  from '~/resume/education'
import contactInfo  from '~/resume/contact'
import familyInfo  from '~/resume/family'
import workInfo  from '~/resume/work'
import carInfo  from '~/resume/position'
import skillInfo  from '~/resume/technology'
import complaintInfo  from '~/resume/shengao'
export default{
	components: {
			hrDept,hrPosition,contract,carInfo,contactInfo,familyInfo,educationInfo,workInfo,skillInfo,complaintInfo
		},
	props:{
		fromResume:{//,
			type:Boolean,
			default:false,
		},
		type:{//button
			type:String,
			default:'primary'
		},
		info:{//id
			
		},
		text:{//button
			type:String,
			default:''
		},
		add:{//
			type:Boolean,
			default:true,
		},
		code:{},//code
		id:{},//id
	},
	data:function(){
		var vm=this; 
		var checkMobile=function(rule,value,callback){
				if(value){
					if(value.length!=11){
						callback(new Error("11"))
					}
				}
				callback();
			}; 
		var checkDiscount=function(rule,value,callback){
				if(value){
					if(parseFloat(value)<0||parseFloat(value)>1){
						callback(new Error(""))
					}
				}
				callback();
			}; 
		var checkEntryDate=function(rule,value,callback){
				if(value&&vm.form.info.formalDate){
					if(moment(value).format('x')>moment(vm.form.info.formalDate).format('x')){
						callback(new Error(""))
					}
				}
				callback();
			}; 
		var checkFormalDate=function(rule,value,callback){
				if(value&&vm.form.info.entryDate){
					if(moment(value).format('x')<moment(vm.form.info.entryDate).format('x')){
						callback(new Error(""))
					}
				}
				callback();
			}; 
		return {
			tabList:[
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				},
				{
					label:'',
					show:true,
				}
				
			],
			resumeId:null,
			hasContract:true,
			checkDiscount:checkDiscount,
			canCreateSalary:true,
			formalDatePickerOptions:{},//
			open:false,//
			saveLoading:false,//liading
			loadingText:"",//
			activeInd:0,//,
			resurmeInfo:{},
			form:{
				info:{
					name:"",//
					code:"",//
					mobile:"",//
					idNum:"",//
					sex:1,//
					dateOfBirth:"",//
					address:"",//
					nowAddress:"",//
					discount:"1.0",//
					deptId:"",//
					email:"",//
					jobId:"",//
					bankCardNo:"",//
					jobOrgId:"",//
					workSystemId:"",//
					jobState:1,//
					entryDate:"",//
					probitionPeriod:"",//
					formalDate:"",//
					quitDate:"",//
					laborDispatch:"Y",//
					workMonths:"",//
					maritalStatus:"",//
					fertilityStatus:"",//
					requireSettlement:"Y",//
					requireAttendance:"Y",//
					salaryId:"",//
					perfId:"",//
					wageForSeniorityAdd:"",//
					otherAllowance:"",//
					trafficAllowance:"",//
					foodAllowance:"",//
					tradeUnions:"Y",//
					tradeUnionsFee:5,//
					jobType:"",//
					salary:"",//
					perf:"",//
				},
				salary:{
					amount:"",//
					socialSecurityNo:"",//
					housingFundNo:"",//
					status:"",//
					householdCode:"",//
					paymentUnit:"",//
					cityCode:"",//
					accinfoId:"",//Id
					accinfoName:"",//
					jobTypeId:'',//id
