<template>
  <div class="page-resume-contact">
    <p class="resume-block-title"></p>
    <el-form label-width="100px" label-position="left" :model="contactInfo" ref="contactInfo"
     :rules="rules">
      <div class="item-block">
        <el-form-item label="" prop="familyphone">
          <el-input placeholder="" v-model="contactInfo.familyphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input placeholder="" v-model="contactInfo.phone"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="email">
          <el-input placeholder="" v-model="contactInfo.email"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="birthplace">
          <el-input placeholder="" v-model="contactInfo.birthplace"></el-input>
        </el-form-item>
        <el-form-item label="" prop="birthcode">
          <el-input placeholder="" v-model="contactInfo.birthcode"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="residence">
          <el-input placeholder="" v-model="contactInfo.residence"></el-input>
        </el-form-item>
        <el-form-item label="" prop="residencecode">
          <el-input placeholder="" v-model="contactInfo.residencecode"></el-input>
        </el-form-item>
      </div>

      <div class="item-block col-3">
        <el-form-item label="" prop="iceperson">
          <el-input placeholder="" v-model="contactInfo.iceperson">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="relation">
          <el-input placeholder="" v-model="contactInfo.relation">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="icepersonphone">
          <el-input placeholder="" v-model="contactInfo.icepersonphone">
          </el-input>
        </el-form-item>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="$router.back()"></el-button>
        <el-button type="primary" @click="next('contactInfo')"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
  	props:{
  		value:{
  			type:Object,
  		}
  	},
    data () {
      return {
      	firstOpen:true,
        contactInfo: {
          familyphone: '',
          phone: '',
          email: '',
          birthplace: '',
          birthcode: '',
          residence: '',
          residencecode: '',
          iceperson: '',
          relation: '',
          icepersonphone: ''
        },
        rules: {
          familyphone: [
            {pattern: /^0\d{2,3}-?\d{7,8}$/i,message:'',trigger: 'blur'}
          ],
          phone: [
          	{required: true, message: '', trigger: 'blur'},
            {pattern:/^1[3|4|5|8][0-9]\d{8}$/i,message:'', trigger: 'blur'}
          ],
          email: [
            {type:'email', message:'',trigger: 'blur'}
          ],
          icepersonphone: [
          	{required: true, message: '', trigger: 'blur'},
            {pattern:/(^1[3|4|5|8][0-9]\d{8}$)|(^0\d{2,3}-?\d{7,8}$)/i,message:'', trigger: 'blur'}
          ],
          birthplace: [
            {required: true, message: '', trigger: 'blur'}
          ],
          iceperson: [
            {required: true, message: '', trigger: 'blur'}
          ],
          residence: [
            {required: true, message: '', trigger: 'blur'}
          ],
          relation: [
            {required: true, message: '', trigger: 'blur'}
          ],
          birthcode:[
          	{pattern:/^[1-9][0-9]{5}$/,message:'',trigger:'blur'}
          ],
          residencecode:[
          	{pattern:/^[1-9][0-9]{5}$/,message:'',trigger:'blur'}
          ],
        }
      }
    },
    created () {
    	if(this.value){
    		Object.assign(this.contactInfo,this.value);
    		this.firstOpen=false;
    	}else if (sessionStorage.contactInfo) {
        this.contactInfo = JSON.parse(sessionStorage.contactInfo)
      }
    },
    watch:{
		"contactInfo":{
        handler(info){
	    		if(this.value&&!this.firstOpen){
	    			Object.assign(this.value,info);
	    		}
	    	},
        deep: true
	    },
		},
    methods: {
    	checkForm(){
    		var flag=true;
    		if(this.$refs.contactInfo){
    			this.$refs.contactInfo.validate((valid)=>{
    				flag=valid;
    			})
    		}
    		return flag;
    	},
      prev () {
        this.$router.push({name: 'position'})
      },
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            sessionStorage.setItem('contactInfo', JSON.stringify(this.contactInfo))
            this.$router.push({name: 'family'})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-contact
    padding-bottom: 50px
</style>



